input {
  width: 170px;
  height: 30px;
  border-radius: 20px;
  padding-left: 10px;
}

body {
  margin: 0;
}

.header {
  width: 100%;
  background-color: #000;
  margin-bottom: 50px;
  display: flex;
}

.search-form {
  width: 100%;
  margin: 15px 0 15px 70%;
  display: flex;
}

.header-logo {
  color: #fff;
  margin-top: 23px;
  margin-left: 20px;
  font-family: Courier New, Courier, monospace;
  font-size: large;
  font-weight: bold;
}

:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.photo-card {
  width: 350px;
  transition: transform var(--animation-duration) var(--timing-function);
  border: 8px double rgba(0, 0, 0, .95);
  border-radius: 0 0 40px 40px;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__item {
  margin-left: 25px;
  padding: 0;
}

.info-item {
  padding-right: 10px;
}

.photo-card:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 250px;
  width: 350px;
}

.gallery__link {
  padding-left: 0;
  list-style: none;
  display: flex;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  justify-content: center;
  margin-bottom: 50px;
  margin-left: auto;
  margin-right: auto;
  display: grid;
}

.load-more {
  width: 150px;
  height: 50px;
  cursor: pointer;
  background-color: #fff;
  border: 3px solid rgba(0, 0, 0, .95);
  margin: 0 auto 50px;
  font-family: Courier New, Courier, monospace;
  font-size: larger;
  font-weight: bold;
  display: block;
}

.load-more:hover, .load-more:focus {
  color: #fff;
  background-color: #000;
  border: 3px solid rgba(0, 0, 0, .95);
}

.submit {
  color: #fff;
  cursor: pointer;
  background-color: #000;
  border: 3px solid #fff;
  border-radius: 20px;
  margin-left: 5px;
  font-family: Courier New, Courier, monospace;
  font-size: larger;
  font-weight: bold;
  display: block;
}

.submit:hover, .submit:focus {
  color: #000;
  background-color: #fff;
  border: 3px solid #fff;
}

.is-hidden {
  display: none;
}

